import { CCard, CCardBody, CCardHeader, CCol, CImg, CRow,
 CForm,CFormGroup,CLabel, CInput, CSelect, CButton,
CModal,
  CModalBody,
  CModalHeader,
  CModalTitle} from '@coreui/react';
import React, {useEffect, useState} from 'react';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import MaterialTable from 'material-table';
import CIcon from '@coreui/icons-react';
import Stepper from 'react-stepper-horizontal';


import SvcMonev from './services-monev';
import swal from 'sweetalert';

import SVCMasterWil from '../master-wil/services-wil';
import BuildFormDynamic from './BuildFormDynamic';
const AddMonev = () =>{

  const [GetNamaPegawaiCtr, SetNamaPegawaictr  ] = useState();
  const [GetListPegawaiCtr, SetListPegawaiCtr] = useState([]);
  const [GetModalPegawai, SetPegawaiModal] = useState(false);
  const [GetJabatanCtr, SetJabatanCtr] = useState([]);
  const [GetProvinsiCtr, SetProvinsiCtr] = useState([]);
  const [GetKabupatenCtr, setKabupatenCtr] = useState([]);
  const [GetKecamatanCtr, setKecamatanCtr] = useState([]);
  const [GetJenisLokpriCtr, setJenisLokpriCtr] = useState([]);
  const [GetBtnLanjutkan, SetBtnLanjutkan] = useState(false);
  const [PostLanjutkanFirts, SetPostLanjutkanFirts] = useState(
    {
      IdUser : 1,
      IdPegawai : 0,
      IdJenisLokpri : 0
    }
  );


  const handleChangeJabatan = () => {

  }

  const GetLoadForm = ()=> {
    SetNamaPegawaictr('Pilih Nama')
    SvcMonev.GetJabatans().then(result => {
        if(result.isSuccess){
          return SetJabatanCtr(result.data);
        }else{
          return SetJabatanCtr([{}]);
        }
    });

    SVCMasterWil.GetProvinsis().then((result) => {
            if(result.isSuccess){
              SetProvinsiCtr(result.data)
            }else{
              SetProvinsiCtr({})
            }
        });

       SvcMonev.GetJenisLokpris().then(result => {
           if(result.isSuccess){
              setJenisLokpriCtr(result.data)
            }else{
              setJenisLokpriCtr([])
            }
        });

  }

  const provinsiChangeHandler = (event) => {

      if(event.target.value !== 0){
        SVCMasterWil.GetKabupatenByIdProv(`?IdProvinsi=${event.target.value}`).then(result => {
            if(result.isSuccess){
              setKabupatenCtr(result.data);
            }
        })
      }else{
          setKabupatenCtr([]);
      }
  }

  const kabupatenChangeHandler = (event) => {
    console.log(event.target.value)
    if(event.target.value !== 0){
        SVCMasterWil.GetKecamatanByIdKab(`?IdKabupaten=${event.target.value}`).then(result => {
            if(result.isSuccess){
              setKecamatanCtr(result.data);
            }
        })
      }else{
          setKecamatanCtr([]);
      }
  }

    const handleChoicePegawi = (idPegawai, fullname) =>{
      SetNamaPegawaictr(fullname);
      SetPegawaiModal(false);
      let PostLanjutkanFirtsNew = {...PostLanjutkanFirts};
      PostLanjutkanFirtsNew['IdPegawai'] = idPegawai;
      SetPostLanjutkanFirts(PostLanjutkanFirtsNew);
    }

    const handleModalPegawai = ()=> {
      SetPegawaiModal(true);
      SvcMonev.GetPegawais().then(result => {
          if(result.isSuccess){
              SetListPegawaiCtr(result.data)
          }else{
              SetListPegawaiCtr([])
          }
      })
    }

    const handleSelectedKategori = (event) => {
        let setValueIdLokpri = event.target.value;
        let PostLanjutkanFirtsNew = {...PostLanjutkanFirts};
        PostLanjutkanFirtsNew['IdJenisLokpri'] = parseInt(setValueIdLokpri);
        SetPostLanjutkanFirts(PostLanjutkanFirtsNew);
    }


    const [GetFormStepper, SetFormStepper] = useState({
      page : 0,
      steps : [],
      dataResult : []
    });

   const nextPage = ()=> {
      let GetFormStepperNew = {...GetFormStepper};
      GetFormStepperNew['page'] = GetFormStepper.page +1;
      SetFormStepper(GetFormStepperNew);
    }
   const previousPage = ()=> {
      let GetFormStepperNew = {...GetFormStepper};
      GetFormStepperNew['page'] = GetFormStepper.page +1;
      SetFormStepper(GetFormStepperNew);
    }


  const handleLanjutkan = ()=> {
    let idUser = PostLanjutkanFirts['IdUser'];
    let idPegawai = PostLanjutkanFirts['IdPegawai'];
    let idJenisLokpri = PostLanjutkanFirts['IdJenisLokpri'];

    if(idPegawai === 0){
      swal("Pegawai","Harap Pilih Pegawai","error");
    }else if(idJenisLokpri === 0){
       swal("Lokpri","Harap Pilih Jenis Lokpri","error")
    }else{

       SetBtnLanjutkan(true);
       consumePostMonev1();
    }
  }

  const consumePostMonev1 = ()=> {
     let idUser = PostLanjutkanFirts['IdUser'];
    let idPegawai = PostLanjutkanFirts['IdPegawai'];
    console.log(idPegawai)
    let idJenisLokpri = PostLanjutkanFirts['IdJenisLokpri'];
    let setParam = `IdUser=${idUser}&IdPegawai=${idPegawai}&IdJenisLokpri=${idJenisLokpri}`;
      SvcMonev.AddMonev(setParam).then(result => {
          if(result.isSuccess){
              let GetFormStepperNew = {...GetFormStepper};
              let stepArrTmp = [];
              result.data.jawabans.map(value =>{
                  let objTitle = {
                    title :value.variabel,
                    dataTest : result.data.jawabans
                  };
                  stepArrTmp.push(objTitle)
              })
              GetFormStepperNew['dataResult']  = result.data.jawabans;
              GetFormStepperNew['steps']  = stepArrTmp;
              return SetFormStepper(GetFormStepperNew);

          }
      })
  }



  useEffect(() => {
    GetLoadForm();
  },[]);


  return(
    <CRow >
      <CCol className="offset-md-1"  md='10' >
        <CCard >
          <CCardHeader>
              <CImg src={process.env.PUBLIC_URL + '/Icon.png'} className="col-md-3 d-flex justify-content-center"/>
          </CCardHeader>
          <CCardBody>
              <CForm className="form-horizontal">
                {

                (GetBtnLanjutkan == false) ?

                  <div>

                      <CFormGroup row>
                        <CCol md="3">
                          <CLabel>Nama</CLabel>
                        </CCol>
                        <CCol xs="12" md="4">
                          <p className="form-control-static">{GetNamaPegawaiCtr}</p>
                        </CCol>
                        <CCol xs="12" md="3">
                          <CButton
                                block
                                color="info"
                                onClick={handleModalPegawai}>
                                  <FontAwesomeIcon className="mr-2"
                                  icon="user" />
                                  Pilih  Pegawai
                          </CButton>
                        </CCol>
                      </CFormGroup>
                <CFormGroup row>
                  <CCol md='3'>
                      <CLabel htmlFor="pilihJabatan"> Pilih Jabatan</CLabel>
                  </CCol>
                  <CCol xs ='12' md='5'>
                       <CSelect custom name="filter_jabatan" id="filter_jabatan" onChange={handleChangeJabatan}>
                             <option value='0'> --Pilih Jabatan -- </option>
                            {

                              GetJabatanCtr.map(item => (
                              <option
                                key={item.idJabatan}
                                value={item.idJabatan}
                              >
                              {item.nama}
                                </option>
                              ))
                           }

                      </CSelect>
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="text-input">Alamat Kantor</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CInput id="text-input" name="text-input" placeholder="Input Alamat" />
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="email-input">Email </CLabel>
                  </CCol>
                  <CCol xs="12" md="5">
                    <CInput type="email" id="email-input" name="email-input" placeholder="Input Email" autoComplete="email"/>

                  </CCol>
                </CFormGroup>


                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="disabled-input">No HP</CLabel>
                  </CCol>
                  <CCol xs="12" md="6">
                    <CInput id="no-hp" name="no-hp" placeholder="Input No Hp"  />
                  </CCol>
                </CFormGroup>

                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="selectSm">Provinsi</CLabel>
                  </CCol>
                  <CCol xs="12" md="5">
                    <CSelect
                      name="provinsi"
                      id="provinsi"
                      onChange={provinsiChangeHandler}>
                           <option value='0'> --Pilih Provinsi -- </option>
                            {

                              GetProvinsiCtr.map(item => (
                                <option
                                  key={item.idProvinsi}
                                  value={item.idProvinsi}
                                >
                                 {item.nama}
                                </option>
                              ))
                           }
                    </CSelect>
                  </CCol>
                </CFormGroup>
                 <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="selectSm">Kabupaten</CLabel>
                  </CCol>
                  <CCol xs="12" md="5">
                    <CSelect
                        name="kabupaten"
                        id="kabupaten"
                        onChange={kabupatenChangeHandler}>
                          <option value='0'> --Pilih Kabupaten -- </option>
                            {
                              GetKabupatenCtr.length !== 0 ?
                                GetKabupatenCtr.map(item => (
                                <option
                                  key={item.idKabupaten}
                                  value={item.idKabupaten}
                                >
                                 {item.nama}
                                </option>
                              )) : null
                            }
                    </CSelect>
                  </CCol>
                </CFormGroup>

                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="selectSm">Kecamatan</CLabel>
                  </CCol>
                  <CCol xs="12" md="5">
                    <CSelect name="kecamatan" id="kecamatan">
                        <option value='0'> --Pilih Kecamatan -- </option>
                            {
                              GetKecamatanCtr.length !== 0 ?
                                GetKecamatanCtr.map(item => (
                                <option
                                  key={item.idKecamatan}
                                  value={item.idKecamatan}
                                >
                                 {item.nama}
                                </option>
                              )) : null
                            }
                    </CSelect>
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="selectSm">Kategori</CLabel>
                  </CCol>
                  <CCol xs="12" md="5">
                     <CSelect
                        name="kategori"
                        id="kategori"
                        onChange={handleSelectedKategori}>
                        <option value='0'> --Pilih Kategori -- </option>
                            {
                                GetJenisLokpriCtr.length !== 0 ?
                                GetJenisLokpriCtr.map(item => (
                                <option
                                  key={item.idJenisLokpri}
                                  value={item.idJenisLokpri}
                                >
                                 {item.nama}
                                </option>
                              )) : null
                            }
                    </CSelect>
                  </CCol>
                </CFormGroup>


                <CButton type="button" size="sm" color="primary" className="col-md-3 offset-md-4" onClick={handleLanjutkan}> Lanjutkan </CButton>
              </div>
                  :
                  <CCard>
                    {console.log(GetFormStepper.page)}
                    <Stepper steps={GetFormStepper.steps} activeStep={GetFormStepper.page} />
                    <div style={{paddingTop:10}}>
                        {GetFormStepper.page === 0 ?
                            <BuildFormDynamic onSubmit={nextPage} onClick={previousPage} DataJson={GetFormStepper.dataResult}/>
                          :
                             <BuildFormDynamic onSubmit={nextPage} onClick={previousPage} DataJson={GetFormStepper.dataResult}/>
                        }
                    </div>

                  </CCard>
                }


              </CForm>
          </CCardBody>
        </CCard>
      </CCol>

      <CModal
              show={GetModalPegawai}
              onClose={() => SetPegawaiModal(!GetModalPegawai)}
              size="lg"
            >
              <CModalHeader closeButton>
                <CModalTitle>Daftar Pegawai </CModalTitle>
              </CModalHeader>
              <CModalBody>
                <MaterialTable
                      title=""
                      options={{
                        headerStyle: {
                          backgroundColor: '#01579b',
                          color: '#FFF'
                        },
                        filtering:true
                      }}
                      columns={[
                        { title: 'ID Pegawai', field: 'idPegawai', type: 'numeric',visible: false ,
                          cellStyle: {
                            backgroundColor: '#039be5',
                            color: '#FFF'
                          },
                          headerStyle: {
                            backgroundColor: '#039be5',
                          }
                        },
                        { title: 'Nama Lengkap', field: 'fullName' },
                        { title: 'Jabatan', field: 'jabatan'  },
                        { title: 'Organisasi', field: 'organisasi'  },

                      ]}
                    data={(GetModalPegawai == true) ? GetListPegawaiCtr : []}
                    actions={[
                      {
                        icon: 'check',
                        tooltip: 'Pilih Pegawai',
                        text:'Update',
                        label:'Update',
                        onClick: (event, rowData) => {
                            handleChoicePegawi(rowData.idPegawai, rowData.fullName)
                        }
                      },
                    ]}
                  />
              </CModalBody>

        </CModal>
    </CRow>


  )
}

export default AddMonev;
